function cc(ch) {
    return ch.charCodeAt(0);
}

export const CharCode = {
    Tab: /*-----------------*/ cc('\t'),
    NewLine: /*-------------*/ cc('\n'),
    Space: /*---------------*/ cc(' '),
    ExclamationMark: /*-----*/ cc('!'),
    QuotationMark: /*-------*/ cc('"'),
    Hashtag: /*-------------*/ cc('#'),
    DollarSign: /*----------*/ cc('$'),
    PercentSign: /*---------*/ cc('%'),
    Ampersand: /*-----------*/ cc('&'),
    Apostrophe: /*----------*/ cc('\''),
    LeftParenthesis: /*-----*/ cc('('),
    RightParenthesis: /*----*/ cc(')'),
    Asterisk: /*------------*/ cc('*'),
    PlusSign: /*------------*/ cc('+'),
    Comma: /*---------------*/ cc(','),
    MinusSign: /*-----------*/ cc('-'),
    FullStop: /*------------*/ cc('.'),
    Slash: /*---------------*/ cc('/'),
    DigitZero: /*-----------*/ cc('0'),
    DigitOne: /*------------*/ cc('1'),
    DigitTwo: /*------------*/ cc('2'),
    DigitThree: /*----------*/ cc('3'),
    DigitFour: /*-----------*/ cc('4'),
    DigitFive: /*-----------*/ cc('5'),
    DigitSix: /*------------*/ cc('6'),
    DigitSeven: /*----------*/ cc('7'),
    DigitEight: /*----------*/ cc('8'),
    DigitNine: /*-----------*/ cc('9'),
    Colon: /*---------------*/ cc(':'),
    Semicolon: /*-----------*/ cc(';'),
    LessThanSign: /*--------*/ cc('<'),
    EqualSign: /*-----------*/ cc('='),
    GreaterThanSign: /*-----*/ cc('>'),
    QuestionMark: /*--------*/ cc('?'),
    AtSign: /*--------------*/ cc('@'),
    CapitalA: /*------------*/ cc('A'),
    CapitalB: /*------------*/ cc('B'),
    CapitalE: /*------------*/ cc('E'),
    CapitalF: /*------------*/ cc('F'),
    CapitalT: /*------------*/ cc('T'),
    CapitalU: /*------------*/ cc('U'),
    CapitalX: /*------------*/ cc('X'),
    CapitalZ: /*------------*/ cc('Z'),
    LeftSquareBracket: /*---*/ cc('['),
    Backslash: /*-----------*/ cc('\\'),
    RightSquareBracket: /*--*/ cc(']'),
    CircumflexAccent: /*----*/ cc('^'),
    LowLine: /*-------------*/ cc('_'),
    GraveAccent: /*---------*/ cc('`'),
    LetterA: /*-------------*/ cc('a'),
    LetterB: /*-------------*/ cc('b'),
    LetterE: /*-------------*/ cc('e'),
    LetterF: /*-------------*/ cc('f'),
    LetterT: /*-------------*/ cc('t'),
    LetterU: /*-------------*/ cc('u'),
    LetterX: /*-------------*/ cc('x'),
    LetterZ: /*-------------*/ cc('z'),
    LeftCurlyBracket: /*----*/ cc('{'),
    Pipe: /*----------------*/ cc('|'),
    RightCurlyBracket: /*---*/ cc('}'),
    Tilde: /*---------------*/ cc('~'),
    Delete: /*--------------*/ 0x007F,
    NBSP: /*----------------*/ 0x00A0
};
